export default {
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		modelValue: {
			type: [String, Number],
			default: ''
		},
		// 
		adjustPosition: {
			type: Boolean,
			default: true
		},
		// 
		maxlength: {
			type: [String, Number],
			default: 6
		},
		// 
		dot: {
			type: Boolean,
			default: false
		},
		// box-line-
		mode: {
			type: String,
			default: 'box'
		},
		// 
		hairline: {
			type: Boolean,
			default: false
		},
		// 
		space: {
			type: [String, Number],
			default: 10
		},
		// 
		focus: {
			type: Boolean,
			default: false
		},
		// 
		bold: {
			type: Boolean,
			default: false
		},
		// 
		color: {
			type: String,
			default: '#606266'
		},
		// 
		fontSize: {
			type: [String, Number],
			default: 18
		},
		// 
		size: {
			type: [String, Number],
			default: 35
		},
		// true
		disabledKeyboard: {
			type: Boolean,
			default: false
		},
		// 
		borderColor: {
			type: String,
			default: '#c9cacc'
		},
		// "."
		disabledDot: {
			type: Boolean,
			default: true
		},
		...uni.$uv?.props?.codeInput
	}
}